<template>
	<view class="main">
		<tou_hand1 handtitle='收益统计详情'></tou_hand1>
		<view class="show">
			<van-tabs v-model="active"  @change="gbtn">
				<van-tab title="本区收益" name="a">
					<view class="tapthree">
						<view class="cdjl-0" v-if="value">
							<view class="cdjl-0-l" @click="onShowDatePicker('range')">
								{{value[0]}}
							</view>-
							<view class="cdjl-0-l" @click="onShowDatePicker('range')">
								{{value[1]}}
							</view>
						</view>
						<van-collapse v-model="activeNames">
							<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
							<van-list v-model="loading" :offset="300" :finished="finished" finished-text="没有更多了" @load="loadMore">
							<van-cell v-for="(item, index) in blist" :key="index" >
							<van-collapse-item :name="index" >
								<template #title>
									<!-- <view>
										<view class="tphand">收益金额:<span style="color: #4B98ED;margin-left: 20upx;">¥{{item.realMoney+item.amount}}</span></view>
										<view class="tpfoot">{{item.startTime}}</view>
									</view> -->
									<view class="taplist">
										<view>
											<view class="tphand">分成金额</view>
											<view class="tpfoot">{{item.detailTime}}</view>
										</view>
										<view class="tapmoney">¥{{item.detailMoney}}</view>
									</view>
								</template>
								<view class="showtap">
									<view class="stone">
										<view class="stoneleft">订单编号：{{item.orderNum}}</view>
										<view class="stoneleft" v-show="isBan==0">订单金额：¥ {{item.actionMoney}}</view>
										<view class="stoneleft" v-show="item.types==0&&isBan==0">分成类型：充值分成</view>
										<view class="stoneleft" v-show="item.types==1&&isBan==0">分成类型：跨区分成</view>
										<view class="stoneleft" v-show="item.types==2&&isBan==0">分成类型：消费分成</view>
										<view class="stoneleft" v-show="item.types==3&&isBan==0">分成类型：临时分成</view>
										<view class="stoneleft" v-show="item.types==4&&isBan==0">分成类型：提现</view>
										<view class="stoneleft" v-show="item.types==5&&isBan==0">分成类型：最低保障</view>
										<view class="stoneleft" v-show="isBan==0">分成比例：{{item.detailBili}}</view>
									</view>
								</view>
							</van-collapse-item>
							
							</van-cell>
							</van-list>
							</van-pull-refresh>
						</van-collapse>
					</view>
				</van-tab>
				<van-tab title="跨区收益"  name="b">
					<view class="tapthree">
						<view class="cdjl-0" v-if="value">
							<view class="cdjl-0-l" @click="onShowDatePicker('range')">
								{{value[0]}}
							</view>-
							<view class="cdjl-0-l" @click="onShowDatePicker('range')">
								{{value[1]}}
							</view>
						</view>
						<van-collapse v-model="activeNames1">
							<van-pull-refresh v-model="yrefreshing" @refresh="yonRefresh">
							<van-list v-model="yloading" :offset="300" :finished="yfinished" finished-text="没有更多了" @load="yloadMore">
							<van-cell v-for="(item, index) in klist" :key="index" >
							<van-collapse-item :name="index">
								<template #title>
									<!-- <view>
										<view class="tphand">收益金额:<span style="color: #4B98ED;margin-left: 20upx;">¥{{item.realMoney+item.amount}}</span></view>
										<view class="tpfoot">{{item.startTime}}</view>
									</view> -->
									<view class="taplist">
										<view>
											<view class="tphand">分成金额</view>
											<view class="tpfoot">{{item.detailTime}}</view>
										</view>
										<view class="tapmoney">¥{{item.detailMoney}}</view>
									</view>
								</template>
								<view class="showtap">
									<view class="stone">
										<view class="stoneleft">订单编号：{{item.orderNum}}</view>
										<view class="stoneleft" v-show="isBan==0">订单金额：¥ {{item.actionMoney}}</view>
										<view class="stoneleft" v-show="item.types==0&&isBan==0">分成类型：充值分成</view>
										<view class="stoneleft" v-show="item.types==1&&isBan==0">分成类型：跨区分成</view>
										<view class="stoneleft" v-show="item.types==2&&isBan==0">分成类型：消费分成</view>
										<view class="stoneleft" v-show="item.types==3&&isBan==0">分成类型：临时分成</view>
										<view class="stoneleft" v-show="item.types==4&&isBan==0">分成类型：提现</view>
										<view class="stoneleft" v-show="item.types==5&&isBan==0">分成类型：最低保障</view>
										<view class="stoneleft" v-show="isBan==0">分成比例：{{item.detailBili}}</view>
									</view>
								</view>
							</van-collapse-item>
							
							</van-cell>
							</van-list>
							</van-pull-refresh>
						</van-collapse>
					</view>
				</van-tab>
			</van-tabs>
		</view>
		<date-picker :show="showPicker" :type="type" class="mask" :value="value" :show-tips="true" :begin-text="'开始'"
		 :end-text="'结束'" :show-seconds="true" @confirm="onSelected" @cancel="onCancel"></date-picker>
	</view>
</template>

<script>
	import tou_hand1 from '../../components/tou_hand1/tou_hand1.vue'
	import datePicker from "../../components/mx-datepicker.vue";
	import {formatDate} from '../../common/formatDate.js'
	export default {
		components: {tou_hand1,datePicker},
		mounted() {

		},
		created() {
			this.show();
			
		},
		data() {
			return {
				active: 'a',
				projectId:'',//小区ID
				activeNames: [''], //当前展开面板的 name
				activeNames1: [''], //当前展开面板的 name
				value: ['请选择开始时间', '请选择结束时间'],
				type: 'range', //时间选择器
				showPicker: false, //时间选择器
				blist:[],//本区收益数据
				klist:[],//跨区收益数据
				loading: false,
				finished: false,
				refreshing: false,
				pageNum: 1, //页码
				pageSize: 10, //每页显示的条数
				yloading: false,
				yfinished: false,
				yrefreshing: false,
				ypageNum: 1, //页码
				ypageSize: 10, //每页显示的条数
				isBan:'',
			}
		},
		onLoad(option) {
			this.isBan=uni.getStorageSync('user').isBan
			this.projectId=option.projectId
			this.show();
			this.loadMore();
			
		},
		methods: {
			show(){
				var nowtime = formatDate(new Date(),'yyyy-MM-dd');				
				var oldtime=formatDate(new Date(new Date().getTime()-2592000000),'yyyy-MM-dd');
				this.value=[oldtime,nowtime]
			},
			gbtn(vul){
				if(vul=='a'){
					this.blist = [];
					this.onRefresh()
				}else if(vul=='b'){
					this.klist = [];
					this.yonRefresh()
				}
			},
			// 本区收益分页
			loadMore() {
				if (this.refreshing) {
				  this.blist = [];
				  this.refreshing = false;
				}
				const that = this;
				let data = {
					projectIds:this.projectId,
					type:0,
					userId: uni.getStorageSync('userId'),
					pageNum:that.pageNum,
					pageSize:that.pageSize,
					startTime:that.value[0],
					endTime:that.value[1]
				}
				that.loading = true;
				that.$base.request1('manager/statistic/money/list', 'GET', data)
					.then(res => {
						that.loading = false;
						if(res.data.rows&&res.data.rows.length>0){
							that.blist = that.blist.concat(res.data.rows);
							that.pageNum += 1;
						}else{
							that.finished = true;
						}
					})
					.catch(err => {
				
					})
			},
			 onRefresh() {
			      // 清空列表数据
			      this.finished = false;
				  this.pageNum=1
			      // 重新加载数据
			      // 将 loading 设置为 true，表示处于加载状态
			      this.loading = true;
			      this.loadMore();
			    },
			
			// 跨区收益分页
			yloadMore() {
				if (this.yrefreshing) {
				  this.klist = [];
				  this.yrefreshing = false;
				}
				const that = this;
				let data = {
					projectIds:this.projectId,
					type:1,
					userId: uni.getStorageSync('userId'),
					pageNum:that.ypageNum,
					pageSize:that.ypageSize,
					startTime:that.value[0],
					endTime:that.value[1]
				}
				that.yloading = true;
				that.$base.request1('manager/statistic/money/list', 'GET', data)
					.then(res => {
						that.yloading = false;
						if(res.data.rows&&res.data.rows.length>0){
							that.klist = that.klist.concat(res.data.rows);
							that.ypageNum += 1;
						}else{
							that.yfinished = true;
						}
					})
					.catch(err => {
				
					})
			},
			 yonRefresh() {
			      // 清空列表数据
			      this.yfinished = false;
				  this.ypageNum=1
			      // 重新加载数据
			      // 将 loading 设置为 true，表示处于加载状态
			      this.yloading = true;
			      this.yloadMore();
			    },	
			//选择时间点击确定
			onSelected(e) { //选择
				this.showPicker = false;
				if (e) {
					this[this.type] = e.value;
					for(var i=0;i<e.value.length;i++){
						e.value[i]=formatDate(new Date(e.value[i]),'yyyy-MM-dd')
					}
					this.value = e.value;
					if(this.active=='a'){
						this.blist = [];
						this.onRefresh()
					}else if(this.active=='b'){
						this.klist = [];
						this.yonRefresh()
					}
				}
			},
			//选择时间点击取消
			onCancel() {
				this.showPicker = false;
				var nowtime = formatDate(new Date(),'yyyy-MM-dd');
				var oldtime=formatDate(new Date(new Date().getTime()-2592000000),'yyyy-MM-dd');
				this.value=[oldtime,nowtime]
				if(this.active=='a'){
					this.blist = [];
					this.onRefresh()
				}else if(this.active=='b'){
					this.klist = [];
					this.yonRefresh()
				}
			},
			//点击时间框选择时间
			onShowDatePicker(type) { //显示
				this.type = type;
				this.showPicker = true;
				this.value = this[type];
			},
		}
	}
</script>

<style lang="scss" scoped>
	.main {
		width: 100%;
		// overflow-x: hidden;
		background-color: #F5F5F5;
		/deep/.van-list__finished-text{
			margin-top: 100upx;
			padding-bottom: 200upx;
		}
		/deep/.van-hairline--top-bottom::after{
			border-width: 0;
		}
		.show {
			/deep/.van-tab {
				font-size: 32upx;
				font-family: PingFang SC Bold, PingFang SC Bold-Bold;
				font-weight: 700;
				color: #999999;
			}
			/deep/.van-cell{
				margin-top: 30upx;
			}
			/deep/.van-tab--active {
				font-size: 32upx;
				font-family: PingFang SC Bold, PingFang SC Bold-Bold;
				font-weight: 700;
				color: #4b98ed;
			}

			/deep/.van-tabs__line {
				width: 91upx !important;
				height: 4upx;
				border-radius: 2px;
				background-color: #4B98ED;
			}

			.tapthree {
				width: 686upx;
				margin: 16upx auto;
				/deep/.van-collapse-item__content{
					padding: 0 20upx!important;
				}
				.cdjl-0 {
					display: flex;
					height: 100upx;
					background: #fff;
					align-items: center;
					justify-content: space-around;
				
					.cdjl-0-l {
						height: 60upx;
						width: 260upx;
						border-radius: 10upx;
						border: 2upx solid #ccc;
						font-size: 30upx;
						line-height: 60upx;
						color: #bbbbbb;
						text-align: center;
					}
				
					.cdjl-0-r {
						width: 120upx;
						height: 60upx;
						font-size: 20upx;
						background: #4e9ff6;
						color: #fff;
						line-height: 60upx;
						text-align: center;
						border-radius: 10upx;
					}
				}
				
				.taplist{
					display: flex;
					justify-content: space-between;
					align-items: center;
					.tphand {
						font-size: 28upx;
						font-family: PingFang SC Bold, PingFang SC Bold-Bold;
						font-weight: 700;
						color: #333333;
					}
								
					.tpfoot {
						font-size: 24upx;
						font-family: PingFang SC Medium, PingFang SC Medium-Medium;
						font-weight: 500;
						color: #333333;
						margin-top: 10upx;
					}
					.tapmoney{
						font-size: 28upx;
						font-family: PingFang SC Bold, PingFang SC Bold-Bold;
						font-weight: 700;
						color: #4b98ed;
					}
				}
				// .tphand {
				// 	font-size: 28upx;
				// 	font-family: PingFang SC Bold, PingFang SC Bold-Bold;
				// 	font-weight: 700;
				// 	color: #333333;
				// }

				// .tpfoot {
				// 	font-size: 24upx;
				// 	font-family: PingFang SC Medium, PingFang SC Medium-Medium;
				// 	font-weight: 500;
				// 	color: #333333;
				// 	margin-top: 10upx;
				// }

				/deep/.van-collapse {
					background-color: #F5F5F5;
				}

				/deep/.van-collapse-item {
					margin-top: 22upx;
				}

				/deep/.van-collapse-item__content {
					padding: 0 30upx;
				}

				/deep/.van-cell {
					align-items: center;
					padding: 10upx 20upx;
				}

				.showtap {
					width: 100%;
					box-sizing: border-box;
					padding: 21upx 0;
					.stone{
						box-sizing: border-box;
						width: 100%;
						.stoneleft{
							font-size: 24upx;
							font-family: PingFang SC Medium, PingFang SC Medium-Medium;
							font-weight: 500;
							color: #666666;
							margin-bottom: 15upx;
						}
					}
				}

			}


		}

	}
</style>
